#page4 {
  margin: 0;
  padding: 0;
  display: grid;
  grid-template-rows: repeat(4, 1fr);
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 3em;
  -ms-flex-align: center;
      align-items: center;
}

.card {
  text-align: center;
  /* border: 2px #2B414D solid; */
  -webkit-box-shadow: 0px 0px 3px 0px rgba(43, 65, 77, 0.4);
          box-shadow: 0px 0px 3px 0px rgba(43, 65, 77, 0.4);  
  border-radius: 5px;
  padding: 2em;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
}

.card-1 {
  grid-row: 2 / 3;
  grid-column: 2 / 3;
}

.card-2 {
  grid-row: 3 / 4;
  grid-column: 3 / 4;
}

.card-3 {
  grid-row: 3 / 4;
  grid-column: 2 / 3;
}

.card-4 {
  grid-row: 2 / 3;
  grid-column: 3 / 4;
}

.card:hover {
  cursor: pointer;
  opacity: 0.8;
  -webkit-box-shadow: 0px 0px 3px 0px rgba(43, 65, 77, 1);
          box-shadow: 0px 0px 3px 0px rgba(43, 65, 77, 1);  
  -webkit-transition: -webkit-box-shadow 0.2s ease-in;  
  transition: -webkit-box-shadow 0.2s ease-in;  
  -o-transition: box-shadow 0.2s ease-in;  
  transition: box-shadow 0.2s ease-in;  
  transition: box-shadow 0.2s ease-in, -webkit-box-shadow 0.2s ease-in;
}

.linkStyle {
  text-decoration: none;
  font-weight: 200;
  color: #2B414D;
  font-size: 1.8em;
}




